import { Alphabet, Dictionary } from '../config/dictionary';

export default {
    data() {
        return {
            dictionaryListId: {},
        }
    },
    methods: {
        dictionaryGetAlphabet(lang) {
            return Alphabet[lang]
        },
        dictionaryGetWordByList(lang) {
            if (!this.dictionaryListId[lang] || this.dictionaryListId[lang].length === 0) {
                this.dictionaryBildListId(lang);
            }
            return Dictionary[this.dictionaryListId[lang].pop()];
        },
        dictionaryBildListId(lang) {
            this.dictionaryListId[lang] = [...Dictionary.keys()]
                .filter(v => Dictionary[v][lang])
                .sort(() => 0.5 - Math.random());
        },
    }
}